<template>
  <nav class="header">
    <div class="icon">
      <img src="../assets/logo.png" alt="Logo" />
    </div>
    <button class="toggle-btn" @click="toggleMenu"><i class="fas fa-bars"></i></button>
    <div class="links-header" :class="{ active: isMenuActive || !isMobile }">
      <div class="links">
        <router-link to="/">Home</router-link>
        <router-link to="/languages">Languages</router-link>
        <router-link to="/services">Services</router-link>
        <router-link to="/event">Event</router-link>
        <router-link to="/contact-us">Contact Us</router-link>
      </div>
      <div class="form">
        <router-link to="/login" class="btn login">Login</router-link>
        <span class="search-icon">
          <img src="../assets/search-icon.png" alt="search-icon" />
        </span>
      </div>
    </div>
  </nav>
</template>

<script>
export default {
  name: 'HeaderPage',
  data() {
    return {
      isMenuActive: false,
      isMobile: window.innerWidth <= 797,
    };
  },
  methods: {
    toggleMenu() {
      this.isMenuActive = !this.isMenuActive;
    },
    checkScreenSize() {
      this.isMobile = window.innerWidth <= 797;
      if (!this.isMobile) {
        this.isMenuActive = false;
      }
    },
    handleRouterChange() {
      // Automatically hide the toggle menu when a router link is clicked
      this.isMenuActive = false;
    },
  },
  mounted() {
    window.addEventListener('resize', this.checkScreenSize);
    // Listen for router change events
    this.$router.afterEach(() => {
      this.handleRouterChange();
    });
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.checkScreenSize);
  },
};
</script>

