<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <div class="login_section">
    <div class="container">
      <div class="header_login">
        <h2>Login</h2>
      </div>
      <div class="form-outline mb-4 form_email">
        <input type="email" id="loginEmail" class="form-control" v-model="email" />
        <label class="form-label" for="loginEmail">Email</label>
      </div>

      <div class="form-outline mb-4 form_password">
        <input type="password" id="loginPassword" class="form-control" v-model="password" />
        <label class="form-label" for="loginPassword">Password</label>
      </div>
      <p v-if="errorMessage" class="text-danger">{{ errorMessage }}</p>
      <button class="sign-in btn btn-primary" @click="login">Sign in</button>
      <p>Not a member? <router-link to="/register">Register</router-link></p>
      <p>Forgot password? <router-link to="/forgot-password">Reset Password</router-link></p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'LoginView',
  data() {
    return {
      email: '',
      password: '',
      errorMessage: '',
    };
  },
  methods: {
    login() {
      const storedPassword = localStorage.getItem(this.email);
      if (storedPassword && this.password === storedPassword) {
        // eslint-disable-next-line no-alert
        alert('Login successful');
        this.errorMessage = '';
        // Redirect to a secure page or dashboard
      } else {
        this.errorMessage = 'Incorrect email or password. Please try again.';
      }
    },
  },
};
</script>

