<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <div class="register_section">
    <div class="container">
      <div class="header_register">
        <h2>Register</h2>
      </div>
      <div class="form-outline mb-4 form_name">
        <input type="text" id="registerName" class="form-control" v-model="name" />
        <label class="form-label" for="registerName">Name</label>
      </div>
      <div class="form-outline mb-4 form_email">
        <input type="email" id="registerEmail" class="form-control" v-model="email" />
        <label class="form-label" for="registerEmail">Email</label>
      </div>

      <div class="form-outline mb-4 form_password">
        <input type="password" id="registerPassword" class="form-control" v-model="password" />
        <label class="form-label" for="registerPassword">Password</label>
      </div>
      <div class="form-outline mb-4 form_repassword">
        <input type="password" id="registerRePassword" class="form-control" v-model="rePassword" />
        <label class="form-label" for="registerRePassword">Re-enter Password</label>
      </div>
      <div class="form-check mb-4">
        <input type="checkbox" id="terms" class="form-check-input" v-model="acceptedTerms" />
        <label class="form-check-label" for="terms">I have read and agree to the terms</label>
      </div>
      <p v-if="errorMessage" class="text-danger">{{ errorMessage }}</p>
      <button class="register btn btn-primary" @click="register">Register</button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'RegisterView',
  data() {
    return {
      name: '',
      email: '',
      password: '',
      rePassword: '',
      acceptedTerms: false,
      errorMessage: '',
    };
  },
  methods: {
    register() {
      // Validate inputs
      if (this.name === '') {
        this.errorMessage = 'Name is required';
        return;
      }
      if (this.email === '') {
        this.errorMessage = 'Email is required';
        return;
      }
      if (this.password === '') {
        this.errorMessage = 'Password is required';
        return;
      }
      if (this.password.length < 8 || !/[a-z]/.test(this.password) || !/[A-Z]/.test(this.password) || !/[0-9]/.test(this.password) || !/[!@#$%^&*]/.test(this.password)) {
        this.errorMessage = 'Password must be at least 8 characters long and include a mix of uppercase letters, lowercase letters, numbers, and symbols';
        return;
      }
      if (this.password !== this.rePassword) {
        this.errorMessage = 'Passwords do not match';
        return;
      }
      if (!this.acceptedTerms) {
        this.errorMessage = 'You must accept the terms and conditions';
        return;
      }
      if (localStorage.getItem(this.email)) {
        this.errorMessage = 'This email is already registered';
        return;
      }
      // Save data to localStorage
      localStorage.setItem(this.email, this.password);
      this.errorMessage = '';
      // eslint-disable-next-line no-alert
      alert('Registration successful');
      // Redirect to login page
      this.$router.push('/login');
    },
  },
};
</script>

