<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <div class="forgot_password_section">
    <div class="container">
      <div class="header_forgot_password">
        <h2>Reset Password</h2>
      </div>
      <div class="form-outline mb-4 form_email">
        <input type="email" id="forgotPasswordEmail" class="form-control" v-model="email" />
        <label class="form-label" for="forgotPasswordEmail">Email</label>
      </div>
      <p v-if="errorMessage" class="text-danger">{{ errorMessage }}</p>
      <p v-if="successMessage" class="text-success">{{ successMessage }}</p>
      <button class="reset-password btn btn-primary" @click="resetPassword">Reset Password</button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ForgotPasswordView',
  data() {
    return {
      email: '',
      errorMessage: '',
      successMessage: '',
    };
  },
  methods: {
    resetPassword() {
      const storedPassword = localStorage.getItem(this.email);
      if (storedPassword) {
        // Simulate password reset
        this.successMessage = 'Password reset link has been sent to your email.';
        this.errorMessage = '';
      } else {
        this.errorMessage = 'Email not found. Please try again.';
        this.successMessage = '';
      }
    },
  },
};
</script>

